import React from 'react'
import styled from 'styled-components'
import { motion } from 'framer-motion'
import MT from '../../assets/Images/milos_tanaskovic.png'

const HomeIntroWrapper = styled(motion.div)`
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    width: 65vw;
    height: 55vh;
    display: flex;

    background: linear-gradient(
        to right,
        ${props => props.theme.body} 50%,
        ${props => props.theme.text} 50%) bottom,
        linear-gradient(
        to right,
        ${props => props.theme.body} 50%,
        ${props => props.theme.text} 50%) top;

    background-repeat: no-repeat;
    background-size: 100% 2px;
    border-left: 2px solid ${props => props.theme.body};
    border-right: 2px solid ${props => props.theme.text};

    z-index: 1;

`;

const SubBox = styled.div`
    width: 50%;
    position: relative;
    display: flex;

    .pic{
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0%);
        width: auto;
        height: -webkit-fill-available;
        @media (max-width: 780px){
            width: 100%;
            height: auto;
        }
    }

`;

const Text = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    font-size: calc(1em + 1.5vw);
    color: ${props => props.theme.body};
    padding: .5rem;
    cursor: pointer;

    &>*:last-child {
        color: ${props => `rgba(${props.theme.bodyRgba}, 0.6)`};
        font-size: calc(0.5rem + 1.5vw);
        font-weight: 300;
    }
`;

export default function HomeIntro({click}) {
    return (
        <HomeIntroWrapper
            initial={{height:0}}
            animate={{height: '55vh'}}
            transition={{ type: 'spring', duration:2, delay:1 }}
        >
            <SubBox>
                <Text>
                    <h1>Hi,</h1>
                    <h2>I'm Milos</h2>
                    <h6>Experienced Front-end React Developer eager to solve engineering problems. Passionate about creating user experience.</h6>
                    {/*Experienced Front-end React Developer eager to solve engineering problems.*/}
                </Text>
            </SubBox>
            <SubBox>
                <motion.div
                    initial={{opacity:0}}
                    animate={{opacity:1}}
                    transition={{ duration:1, delay:2 }}
                >
                    <img className='pic' src={MT} alt='Milos Tanaskovic CodeDancing' />
                </motion.div>
            </SubBox>
        </HomeIntroWrapper>
    )
}
